
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import recommend from '@/components/Recommend'
const tabs = () => ['个性推荐', '歌单', '主播电台', '排行榜']
var el = null;
export default {
components: {
XHeader,
Tab,
TabItem,
Swiper,
SwiperItem,
recommend
},
methods:{
update(){

},
onSwiperItemIndexChange (index) {

},
},
data () {
return {
tabs:tabs(),
index: 0,
}
},
created:function(){
el = document.getElementsByClassName('vux-swiper-item');

},
watch:{
'$store.state.wrapHeight':function(){
this.$nextTick(()=>{
this.$refs.swiper.xheight = this.$store.state.wrapHeight+'px';
this.$store.commit('setWrapHeight', this.$store.state.wrapHeight);
});
}
},
mounted(){
this.$nextTick(()=>{
this.$refs.swiper.xheight = this.$store.state.wrapHeight+'px';

});
}
}



