
require('swiper/dist/css/swiper.css')
import axios from 'axios';
import api from '@/api/common'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import {mapActions,mapState,mapMutations} from 'vuex'
import recommendSong from '@/components/List'
import privateSong from '@/components/Private'

const imgList = [
'static/banner1.jpg',
'static/banner2.jpg',
'static/banner3.jpg',
'static/banner4.jpg'
]

export default {
name: 'recommend',
data () {
var that = this;
return {
notNextTick: true,
swiperOption: {
pagination: '.swiper-pagination',
passiveListeners : false,
touchReleaseOnEdges:false,
autoplay: 2400,
loop: false,
nested:true,
observer:true,
observeParents:true,
updateOnImagesReady : true,
onImagesReady(swiper){

that.$store.commit('setHeight', swiper.height);

var findDom = document.getElementById('find');
var imgItem = document.getElementsByClassName('banner-item');
setTimeout(()=>{
console.log(swiper);
console.log(findDom.scrollHeight);
that.$store.commit('setWrapHeight', findDom.scrollHeight);
},100);


}
},
imglist:imgList,
index: 1,
}
},

created:function(){
axios.get(api.banner).then((res)=>{
this.imglist = res.data.banners;
}).catch((res)=>{
console.log(res);
});

},
components:{
swiper,
swiperSlide,
Grid,
GridItem,
recommendSong,
privateSong
}
}



